import {NoteNode as NN, NoteGetter, NotePoster, NoteConfig} from './types'
import { None, URLType} from '../types'
import {useNote} from './hooks'

// export function Note (props: {
// ]}): JSX.Element

export function Note (props: {
    children: undefined & ((note: NN, setNote: ((
            updateGetURL?: ((pre:NN)=>None<URLType|string>)|None<URLType|string>,
            updateGetter?: NoteGetter|null,
            updateConfig?: Partial<NoteConfig>
        ) => void), postNote: ((
            updatePoster?: NotePoster|null,
            updateConfig?: Partial<NoteConfig>
        ) => void)
    ) => Element | null)
}): Element | null

export function Note (props: any) {
    return props?.children(useNote(props))
}
export * from './hooks'
export * from './types'
